import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/:idBitacora',
    name: 'Home',
    component: Home
  },
  {
    path: '/',
    name: 'Bitacoraconfiguracion',
    // route level code-splitting
    // this generates a separate chunk (incidencia.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "incidencia" */ '../views/Bitacoraconfiguracion.vue')
  },
  {
    path: '/misbitacoras',
    name: 'Misbitacoras',
    // route level code-splitting
    // this generates a separate chunk (incidencia.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "misbitacoras" */ '../views/Misbitacoras.vue')
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router